.card {
  padding-bottom: 30px;
  margin: auto;
}
@media (min-width: 640px) {
  .card {
    display: grid;
    grid-template-columns: repeat(2, 28%);
    justify-content: center;
    align-items: center;
    gap: 40px 120px;
  }
}
@media (min-width: 1150px) {
  .card {
    display: grid;
    grid-template-columns: repeat(4, 23%);
    gap: 25px;
    margin-bottom: 60px;
    justify-content: center;
    padding: 0 25px 0 25px;
  }
}
@media (max-width: 639px) {
  .card {
    margin: auto;
    display: grid;
    grid-template-columns: repeat(1, 370px);
    justify-content: center;
    align-items: center;
    gap: 30px;
    max-width: 400px;
  }
}
.card__components {
  width: 100%;
  border: 1px solid #cce7d0;
  padding: 10px;
  min-width: 250px;
  border-radius: 10px;
  cursor: pointer;
  box-shadow: 20px 20px 30px rgba(0, 0, 0, 0.02);
  transition: 0.2s ease;
}
.card__components:hover {
  box-shadow: 20px 20px 30px rgba(0, 0, 0, 0.06);
  transform: scale(105%);
  transition: 150ms ease-in-out;
}
.card__components .subcomponent {
  display: grid;
  padding: 10px 0;
  grid-template-columns: auto auto;
  grid-template-rows: auto auto auto auto;
}
.card__components .subcomponent p {
  margin-top: 0;
  margin-bottom: 0;
}
.card__components .subcomponent a {
  justify-self: end;
  padding-right: 5px;
}
.card img {
  width: 100%;
  border-radius: 15px;
}

.addidas {
  color: var(--addidas-text);
  font-size: 12px;
}

.sub {
  font-size: 14px;
  grid-column: 1/3;
  color: var(--nav-text);
  padding-top: 7px;
  font-weight: 600;
}

.stars i {
  padding-top: 7px;
  font-size: 12px;
  color: var(--star-color);
  grid-column: 1/2;
}

.money {
  padding-top: 7px;
  font-size: 17px;
  font-weight: 700;
  color: var(--nav-hover);
  grid-column: 1/2;
}

.cart1 {
  padding-left: 10px;
  width: 40px;
  height: 40px;
  border-radius: 50px;
  font-weight: 600;
  line-height: 40px;
  color: var(--cart-color);
  background-color: var(--cart-bg);
  grid-column: 2/3;
  grid-row: 3/4;
}

.repair {
  margin-top: 30px;
  margin-bottom: 25px;
  margin-right: -10px;
  background: url(/img/banner/b2.jpg);
  background-size: cover;
  padding: 80px;
  display: flex;
  flex-direction: column;
  text-align: center;
  justify-content: center;
