@use "../util/" as *;
main {
    top: 0;
    position: relative;
    overflow-x: hidden;
}

#page-header{
    background-image: url("/img/banner/b1.jpg");
    background-size: cover;
    background-position: top 25% right 0; //this make the background to move
    height: 40vh;
    width: 100%;
    text-align: center;
    padding: 25px 80px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 14px;
    margin-bottom: 70px;

    @include breakpoint-down (medium) {
        background-position: top 30% right 30%;
        height: 60vh;
        padding: 25px 80px;
    }

    h2, p {
      color: var(--white-text);
      text-align: center;
      margin: 5px;
    }
    h2 {
      font-size: 42px;
      line-height: 54px;
      margin-bottom: 0;
    }
}
